<template>
  <div id="app">
    <router-view/>
    <footer class="menu-wrap">
      <router-link to="/" class="menu-item">
        <i class="iconfont iconfont-shouye"></i>
        <p class="menu-title">首页</p>
      </router-link>
      <router-link to="/dynamic" class="menu-item">
        <i class="iconfont iconfont-haoyou"></i>
        <p class="menu-title">动态</p>
      </router-link>
      <router-link to="/search" class="menu-item">
        <i class="iconfont iconfont-sousuo"></i>
        <p class="menu-title">发现</p>
      </router-link>
      <router-link to="/mine" class="menu-item">
        <i class="iconfont iconfont-yonghu"></i>
        <p class="menu-title">我的</p>
      </router-link>
    </footer>
  </div>
</template>
<script>
export default {
  name: 'App',
  data() {
    return {
      selected: '首页'
    }
  },
  methods: {
    gotoPage(path) {
      this.$router.push(path)
    }
  }
}

</script>
<style lang="scss" scoped>
#app {
  height:100%;
  overflow:hidden;
}
.menu-wrap {
  display: flex;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  text-align: center;
  border-top: 1px solid #ddd;
  padding:5px 0;
  .menu-item {
    -webkit-box-flex: 1;
    display: block;
    flex: 1;
    &.router-link-exact-active {
      color:$primary-c;
    }
    .iconfont {
      font-size: 18px;
    }
    .menu-title {
      padding-top:5px;
    }
  }
}

</style>



