<template>
  <div class="top">
    <header class="header">
      <form action="">
        <input type="search">
        <label for="cameraFile">
          <i class="iconfont iconfont-tupian"></i>
        </label>
        <input type="file" accept="image/*" capture="camera" style="display:none;" id="cameraFile">
      </form>
    </header>
    <div class="container">
      <router-view/>
      <p v-for="item in 100" :key="item">这是个测试文本，不要慌 {{item}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Home',
  data() {
    return {}
  }
}

</script>
<style scoped lang="scss">
.header {
  padding: 5px 0 0 12px;
  position: absolute;
  left: 0;
  top: 0;
  height: 40px;
  width: 100%;
  background-color: #fff;
  form {
    height: 100%;
    display: -webkit-flex;
    -webkit-flex-direction: row;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  input {
    height: 100%;
    border: 1px solid #ddd;
    color: #000;
    border-radius: 5px;
    padding-left: 6px;
    width: 90%;
  }
  label {
    text-align: center;
    flex: 1;
  }
}

.container {
  top: 45px;
  bottom: 55px;
  width: 100%;
  overflow: auto;
  position: absolute;
  z-index: 10;
}

</style>



