import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Follow from '@/components/Follow'
import Recommend from '@/components/Recommend'
import Hot from '@/components/Hot'
import Dynamic from '@/components/Dynamic'
import Search from '@/components/Search'
import Mine from '@/components/Mine'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    name: 'Home',
    component: Home,
    children: [{
      path: 'follow',
      name: 'Follow',
      component: Follow
    }, {
      path: 'recommend',
      name: 'Recommend',
      component: Recommend
    }, {
      path: 'hot',
      name: 'Hot',
      component: Hot
    }]
  }, {
    path: '/dynamic',
    name: 'Dynamic',
    component: Dynamic
  }, {
    path: '/search',
    name: 'Search',
    component: Search
  }, {
    path: '/mine',
    name: 'Mine',
    component: Mine
  }]
})



